*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #333;
  --secondary-color: #666;
  --border-color: #eee;
  --background-color: #fff;
  --hover-color: #f5f5f5;
  --completed-color: #888;
  --high-color: #ff4444;
  --medium-color: #ffa500;
  --low-color: #4caf50;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: var(--background-color);
  display: grid;
  grid-template-columns: 1fr 4fr;
  min-height: 100vh;
}

/* Sidebar */
.sidebar {
  background: #e6e5e1;
  padding: 20px;
  height: 100vh;
  overflow-y: auto;
  position: sticky;
  top: 0;
  left: 0;
}

.sidebar h2 {
  margin-bottom: 20px;
  color: var(--primary-color);
}

.sidebar-header {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
}

.sidebar-header button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: large;
}

.dropdown {
  border-top: #cfcfcf solid 1px;
  margin: 10px 0;
}

.dropdown-header {
  padding: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.dropdown-header i {
  margin-right: 10px;
}

.dropdown-content {
  padding-left: 20px;
}

.dropdown.inactive .dropdown-content {  
  display: none;
}

.menu-item {
  padding: 8px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.menu-item i {
  margin-right: 10px;
}

.menu-item:hover {
  background: #f5f5f5;
}

.sidebar-footer {
  padding: 1rem;
  border-top: 1px solid #ddd;
  margin-top: auto;
}

.add-todo-btn {
  width: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  border: none;
  background: #c9c9c9;
  border-radius: 4px;
  cursor: pointer;
}

.add-todo-btn:hover {
  color: white;
  background: #797979;
}

.sidebar.collapsed .sidebar-content,
.sidebar.collapsed .sidebar-footer {
  display: none;
}

/* Main content */
#main {
  padding: 20px;
  box-sizing: border-box;
  background-color: #f5f5f5;
}

header {
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 4px;
}

header h1 {
  margin: 0;
  margin-left: 10px;
}

.title-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  background-color: white;
  display: flex;
  align-items: center;
  padding: 8px;
  margin: 8px 0;
  transition: background-color 0.3s, text-decoration 0.3s;
  cursor: pointer;
}

li:hover {
  background-color: #d0e3e4;
}

li.completed {
  background-color: #f5f5f5;
  color: #a1a1a1;
  text-decoration: line-through;
}

li.completed span.due-date {
  background-color: #dddddd;
  color: #a1a1a1;
  text-decoration: line-through;
}

li span.due-date {
  background-color: #929292;
  color: white;
  padding: 2px 4px;
  margin-right: 8px;
  border-radius: 4px;
}

li input[type="checkbox"] {
  margin-right: 8px;
}

.priority {
  padding: 2px 4px;
  margin-left: 8px;
  border-radius: 4px;
  color: white;
}

.priority.high {
  background-color: #ff6666;
}

.priority.medium {
  background-color: #ffcc66;
}

.priority.low {
  background-color: #66cc66;
}

/* Modal Styles */
.modal {
    display: flex;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4);
    align-items: center;
    justify-content: center;
}

.modal-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    width: 500px;
    max-width: 90%;
    max-height: 90vh;
    overflow-y: auto;
}

.form-group {
    margin-bottom: 16px;
}

.form-group label {
    display: block;
    margin-bottom: 4px;
    font-weight: bold;
}

.form-group input[type="text"],
.form-group input[type="date"],
.form-group select,
.form-group textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
}

.form-group textarea {
    min-height: 60px;
    resize: vertical;
}

.title-input {
    width: 100%;
    font-size: 1.2em;
    border: none;
    outline: none;
    border-bottom: 1px solid #ddd;
    padding: 8px 0;
    margin-bottom: 16px;
}

.modal-buttons {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    margin-top: 20px;
}

.modal-buttons button {
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: 500;
    transition: opacity 0.2s;
}

.modal-buttons button:hover {
    opacity: 0.9;
}

.save-btn {
    background: #4CAF50;
    color: white;
}

.cancel-btn {
    background: #f44336;
    color: white;
}

.flex-row {
    display: flex;
    gap: 20px;
}

.flex-row > * {
    flex: 1;
}

/* Media queries */
@media screen and (max-width: 1024px) {
  body {
    grid-template-columns: 25% 75%;
  }
}

@media screen and (max-width: 768px) {
  body {
    grid-template-columns: 30% 70%;
  }
}

@media screen and (max-width: 480px) {
  body {
    grid-template-columns: 100%;
  }
  
  .sidebar {
    position: fixed;
    z-index: 100;
    transform: translateX(-100%);
  }
}

/* To be done */
.add-category {
  display: none;
}

.placeholder-text {
  color: #999;
  font-style: italic;
}
